.App {
  text-align: center;
}
.App-logo {
  height: 100%;
  pointer-events: none;
}

.card-header:first-child {
    border-radius: 0;
}
.logo{
  height:100%;
}

@keyframes transition {
  from {opacity: 0;}
  to {opacity: 1;}
}
.transition{
  width:100%;
  height: 100%;
  opacity: 1;
  animation-name: transition;
  animation-duration: 0.3s;
}

@keyframes transitionIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
.fadeIn{
  opacity: 1;
  animation-name: transitionIn;
  animation-duration: 0.5s;
  animation-timing-function: ease-in;
}

