@media screen and (orientation: portrait){
  .bigText{
    font-size: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 92vh;
    width: 100%;
    margin:0;
    color: white;
    text-shadow: 4px 4px black;
    opacity: 1;
    animation-name: transition;
    animation-duration: 0.3s;
  }
  
  .card-header{
    height:8vh;
    margin:0;
    padding: 5px;
    background-color: #A30046;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}
@media screen and (orientation:landscape){
  .card-header{
    height:12vh;
    margin:0;
    padding: 0.3rem;
    background-color: #A30046;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}
  .bigText{
    font-size: 30vw;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 88vh;
    width: 100%;
    margin:0;
    color: white;
    text-shadow: 4px 4px black;
    opacity: 1;
    animation-name: transition;
    animation-duration: 0.3s;
  }
}
.form-control{
  width: 90vw;
  margin: 0 auto;
}

.form-group{
  margin-top:2vh;
}

.card{
  margin:0;
  border: 0px solid rgba(0,0,0,.125);
  border-radius: 0;
}

.cardActive{
  background-color: lightgreen;
  opacity: 1;
  animation-name: transition;
  animation-duration: 0.3s;
}

.cardWarning{
  background-color: orange;
  opacity: 1;
  animation-name: transition;
  animation-duration: 0.3s;
}

.cardWaiting{
  background-color: lightskyblue;
  opacity: 1;
  animation-name: transition;
  animation-duration: 0.3s;
}

.cardEnd{
  background-color: red;
  opacity: 1;
  animation-name: transition;
  animation-duration: 0.3s;
}

.btn-primary{
  background-color: #A30046;
  border-color: #A30046;
}

.chkbox-timer{
  margin-top:15px;
  margin-right: 10px;
  height: 20px;
  width: 20px;
  vertical-align: sub;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

